import React, { useState, useEffect } from "react";

import PropTypes from "prop-types";

import { withRouter } from "react-router-dom";

import { auth } from "../../firebase";

import authentication from "../../services/authentication";

import EmptyState from "../EmptyState";

// import { ReactComponent as CabinIllustration } from "../../illustrations/cabin.svg";
import { ReactComponent as InsertBlockIllustration } from "../../illustrations/insert-block.svg";
// import { responsiveFontSizes } from "@material-ui/core";
const HomePage = (props) => {
    const [bgUrl, setBgUrl] = useState({
        error: null,
        isLoaded: false,
        url: null,
    });

    const { user } = props;
    const signInWithEmailLink = () => {
        const { user } = props;

        if (user) {
            return;
        }

        const emailLink = window.location.href;

        if (!emailLink) {
            return;
        }

        if (auth.isSignInWithEmailLink(emailLink)) {
            let emailAddress = localStorage.getItem("emailAddress");

            if (!emailAddress) {
                this.props.history.push("/");

                return;
            }

            authentication
                .signInWithEmailLink(emailAddress, emailLink)
                .then((value) => {
                    const user = value.user;
                    const displayName = user.displayName;
                    const emailAddress = user.email;

                    this.props.openSnackbar(
                        `${displayName || emailAddress} olarak giri yapld`
                    );
                })
                .catch((reason) => {
                    const code = reason.code;
                    const message = reason.message;

                    switch (code) {
                        case "auth/expired-action-code":
                        case "auth/invalid-email":
                        case "auth/user-disabled":
                            this.props.openSnackbar(message);
                            break;

                        default:
                            this.props.openSnackbar(message);
                            return;
                    }
                })
                .finally(() => {
                    this.props.history.push("/");
                });
        }
    };

    useEffect(() => {
        signInWithEmailLink();
        fetch(
            "https://source.unsplash.com/collection/4928529&orientation=landscape"
        )
            .then((res) => {
                setBgUrl({
                    isLoaded: true,
                    url: res.url,
                });
            })
            .catch((error) =>
                setBgUrl({
                    isLoaded: true,
                    error,
                })
            );
    }, []);
    return (
        <>
            {user ? (
                <img alt="landscape" src={bgUrl.url} />
            ) : (
                <EmptyState
                    image={<InsertBlockIllustration />}
                    title="RMUIF"
                    description="Supercharged version of Create React App with all the bells and whistles"
                />
            )}
        </>
    );
};
